/***     ()*/

/**
 *  
 */

export const selectItem = (item, checked) => {
    return {
        type: 'CHECK_ITEM',
        payload: {
            item: item,
            checked: checked
        }
    }
};

/**
 *  
 */

export const selectModule = (item, checked) => {
    return {
        type: 'CHECK_MODULE',
        payload: {
            item: item,
            checked: checked
        }
    }
};

/**
 *     
 */

export const clearAll = () => {
    return {
        type: 'CLEAR_ALL',
    }
};

/**
 *    
 */

export const deleteItem = (id) => {
    return {
        type: 'DELETE_ITEM',
        payload:  {
            id: id
        }
    }
};

/**
 *  +
 */

export const countPlus = (item) => {
    return {
        type: 'COUNT_PLUS',
        payload: {
            item: item
        }
    }
};

/**
 *  -
 */

export const countMinus = (item) => {
    return {
        type: 'COUNT_MINUS',
        payload: {
            item: item,
        }
    }
};


