import React, {Component} from "react";
import './FurnitureBlock.css';

/**
 *  
 */

/***   */
class FurnitureItem extends Component {

    render() {
        const {
            className,
            itemClass,
            title,
            itemName,
            description,
            white
        } = this.props;

        return (
            <div className={className}>
                <div className= {`fur-item btn-navy btn-border-rev-o ${itemClass}`}>
                    <div className={"item-info-title "+ white}>{title}<p>{itemName}</p></div>
                    <div className={"item-info "+ white}>{description}</div>
                </div>
            </div>
        )
    }
}

/***   */
class FurnitureBlock extends Component {

    render() {
        return (
            <div className="furniture container">
                <div className="row furniture-1">
                    <div className="col-4">
                        <div className="title-block furniture-titles">
                            <div className="title"> </div>
                            <div className="line0"/>
                            <div className="sub-title"> </div>

                            <div className="all-btns-an black-btn"> </div>
                        </div>
                    </div>

                    <FurnitureItem
                        className="col-3"
                        itemClass="item1"
                        title="  "
                        itemName=""
                        description={<React.Fragment>
                            <div><b>:</b>  </div>
                            <div><b>: </b> 12710694 </div>
                            <div><b>:</b>  </div>
                        </React.Fragment>}
                    />
                    <FurnitureItem
                        className="col-2"
                        itemClass="item2"
                        title=""
                        itemName=""
                        description={<React.Fragment>
                            <div><b>:</b> 2087670</div>
                            <div><b>:</b>  </div>
                        </React.Fragment>}
                    />
                    <FurnitureItem
                        className="col-3"
                        itemClass="item3"
                        title="  "
                        itemName=""
                        description={<React.Fragment>
                            <div><b>:</b> 7111095</div>
                            <div><b>:</b>  </div>
                        </React.Fragment>}
                    />
                </div>

                <div className="row furniture-2">
                    <FurnitureItem
                        className="col-3"
                        itemClass="item4"
                        title=""
                        white="whit"
                        itemName=""
                        description={<React.Fragment>
                            <div><b>:</b> 7111095</div>
                            <div><b>:</b>  </div>
                        </React.Fragment>}
                    />
                    <FurnitureItem
                        className="col-3"
                        itemClass="item5"
                        title=""
                        itemName=""
                        description={<React.Fragment>
                            <div><b>:</b> 7320085 </div>
                            <div><b>:</b>  </div>
                        </React.Fragment>}
                    />
                    <FurnitureItem
                        className="col-3"
                        itemClass="item6"
                        title=""
                        itemName=""
                        description={<React.Fragment>
                            <div><b>:</b> 7620482</div>
                            <div><b> :</b> 73204</div>
                            <div><b>:</b>  </div>
                        </React.Fragment>}
                    />
                    <FurnitureItem
                        className="col-3"
                        itemClass="item7"
                        title=""
                        itemName=""
                        description={<React.Fragment>
                            <div><b>:</b> Joy 8</div>
                            <div><b>:</b> 1008097</div>
                            <div><b>:</b> </div>
                        </React.Fragment>}
                    />
                </div>
            </div>
        )
    }
}

export default FurnitureBlock;


