import React, {Component} from 'react';
import BottomBlock from "../BottomBlock/BottomBlock";
import './Footer.css';

/**
 *  
 */

/***  */
class FooterLinks extends Component {

    render() {
        const {
            title,
            links,
        } = this.props;
        return (
            <div className="footer-nav col-2">
                <ul>
                    <li className="tit">{title}</li>
                    {
                        links.map((links, index) => <li key={'footer'+index}><a href="">{links.name}</a></li>)
                    }
                </ul>
            </div>
        )
    }
}

/*** */
class Footer extends Component {

    render() {
        const footerLinks = {
            about: [
                {name:' '},
                {name:' '},
                {name:' '},
                {name:''},
                {name:''}
            ],
            furniture: [
                {name:' '},
                {name:''},
                {name:''},
                {name:' '},
                {name:''}
            ],
            communication: [
                {name:' '},
                {name:''},
                {name:' '},
                {name:'  '},
                {name:''}
            ]};
        return (
            <div>
                <BottomBlock />
                    <footer className="page-footer container">
                        <div className="row foot-row">
                            <FooterLinks
                                title=" "
                                links={footerLinks.about}
                            />
                            <FooterLinks
                                title=" "
                                links={footerLinks.furniture}
                            />
                            <FooterLinks
                                title=""
                                links={footerLinks.communication}
                            />
                            <div className="contacts col-4">
                                <div className="tit">:</div>
                                : 8(495)712-55-09, 8(495)712-57-07<br/>
                                E-mail: info@mf-woodcraft.ru<br/>
                                 :  . . .<br/>
                                  : <span>8(495)712-55-09</span><br/>
                                  <span>E:mail zakaz@mf-woodcraft.ru</span>
                            </div>
                        </div>
                    </footer>
            </div>
        );
    }
}

export default Footer;




